function richTextParse(data) {
    //
    //	let nameArr = ['a','abbr','b','blockquote','br','code','col','colgroup','dd','del','div','dl','dt','em','fieldset','h1','h2','h3','h4','h5','h6','hr','i','img','ins','label','legend','li','ol','p','q','span','strong','sub','sup','table','tbody','td','tfoot','th','thead','tr','ul'];
    let nameArr = ['a', 'b', 'code', 'dd', 'div', 'dl', 'dt', 'em', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'i', 'img', 'label', 'li', 'ol', 'p','pre', 'span', 'strong', 'sub', 'sup', 'table', 'tbody', 'td', 'tfoot', 'th', 'thead', 'tr', 'ul'];
    //
    let tree = [];
    //
    let index = 0;
    //
    let tagArr = data.match(/<[^>]*>/gi);
    //
    let indexArr = [];
    //
    for (let i = 0; i < tagArr.length; i++) {
        //
        indexArr.push(data.indexOf(tagArr[i]))
        //
        data = data.replace(tagArr[i], '')
    }

    //
    let cls, alt, src, width, height, step, href;

    let i = 0;
    //
    while (i < tagArr.length - 1) {
        //
        tree[index] = sendInfoTree(i)
        index++;
    }
    return tree;

    //tree
    function sendInfoTree(idx) {
        getStartInfo(tagArr[idx])
        var label = tagArr[idx].match(/<*([^> ]*)/)[1];
        //
        var obj = {
            name: checkName(label),
            attrs: {
                class: cls + ' rich-' + label
            },
            children: []
        }
        //
        if (label === 'img') {
            //img
            obj.attrs['src'] = src;
            obj.attrs['alt'] = alt;
            getImgSize(src).then((size)=>{
                obj.attrs['width'] = size.width
                obj.attrs['height'] = size.height
            })
        }else if (step === 0) {
            //img--
            obj.children.push({
                type: 'text',
                text: ''
            })
        } else if (tagArr[idx + 1] === '</' + label + '>') {
            //
            obj.children.push({
                type: 'text',
                text:replaceStr(data.substring(indexArr[idx], indexArr[idx + 1]))
            })
            //
            i++;
        } else {
            //
            //
            if (indexArr[idx] !== indexArr[idx + 1]) {
                obj.children.push({
                    type: 'text',
                    text: replaceStr(data.substring(indexArr[idx], indexArr[idx + 1]))
                })
            }
            //
            i++;
            while (i < tagArr.length - 1) {
                obj.children.push(sendInfoTree(i));
                //
                if (indexArr[i - 1] !== indexArr[i]) {
                    obj.children.push({
                        type: 'text',
                        text: replaceStr(data.substring(indexArr[i - 1], indexArr[i]))
                    })
                }
                //
                if (tagArr[i] === '</' + label + '>') break;
            }
        }
        //a
        if(label === 'a'){
            obj.children[obj.children.length-1].text += '' + href + '';
        }
        i++;

        return obj;
    }

    // 
    function replaceStr(str){
        return str.replace(/^\s+|\s+$/g, '');
    }

    //
    function getStartInfo(str) {
        //
        cls = matchRule(str, 'class');
        src = '';
        alt = '';
        width = '';
        height = '';
        //img
        if (str.match(/<*([^> ]*)/)[1] === 'img') {
            src = matchRule(str, 'src');
            alt = matchRule(str, 'alt');
        }else if(str.match(/<*([^> ]*)/)[1] === 'a'){
            href = matchRule(str, 'href');
        }
        //
        step = str[str.length - 2] === '/' ? 0 : 1;
    }

    //
    function matchRule(str, rule) {
        let value = '';
        let re = new RegExp(rule + '=[\'"]?([^\'"]*)');
        if (str.match(re) !== null) {
            value = str.match(re)[1];
        }
        return value;
    }
    //
    function checkName(str) {
        let name = 'div';
        for (let i = 0; i < nameArr.length; i++){
            if (str === nameArr[i]) {
                name = str;
                break;
            }
        }
        return name;
    }
    // 
    function getImgSize(src){
        return new Promise((s,e)=>{
            let obj = {}
            let newImg = document.createElement('img');
            newImg.src = src;
            newImg.onload = function(event){
                obj.width = event.target.naturalWidth;
                obj.height = event.target.naturalHeight;
                s(obj)
            }
        })
    }
}

module.exports = {
    go: richTextParse
};


